<template>
	<v-app id="app">
		<div id="devoluciones" class="myTable">
			<!--HEAD DE LA PAGINA -->
			<vue-headful
				title="devoluciones - Aranda Distribuciones"
			></vue-headful>

			<h2>Listado de devoluciones</h2>

			<b-button @click="testFetch" class="mb-4" title="Recargar" variant="light">
				<v-icon dark style="color: black">mdi-cached</v-icon>
				Actualizar
			</b-button>
			<!-- ================ALTA PLANES======================== -->
			<b-button
				class="mb-4 ml-2"
				v-b-modal.modal-alta
				@click="altadevolucion()"
				title="Nuevo Pago de devolucion"
				style="color: white"
			>
				<v-icon dark> mdi-plus </v-icon>
				Nuevo devolucion
			</b-button>
			<b-modal no-close-on-backdrop id="modal-alta" hide-footer ref="modalalta">
				<template #modal-title>
					<h5 class="modal-title">Alta</h5>
				</template>
				<devoluciones-alta :updateTable="testFetch" />
			</b-modal>
			<!-- =========================================================== -->
			
			<b-button
				class="mb-4 ml-2"
				variant="danger"
				id="btn_del_full"
				title="Eliminar todos los registros"
				style="color: white"
				:disabled="btn_del_full"
				v-b-modal.modal-eliminarTodo
			>
				<v-icon class="mr-2" style="color: white"> mdi-delete </v-icon>
				Eliminar
			</b-button>

			<!-- ================ELIMINAR VARIOS PLANES======================== -->
			<div>
				<b-modal
					ref="my-modal"
					id="modal-eliminarTodo"
					hide-footer
					title="Eliminar"
					ok-only
				>
					<div class="d-block text-center" v-if="selected.length === rows">
						<h3>¿Está seguro de eliminar todos los registros?</h3>
					</div>
					<div class="d-block text-center" v-else>
						<h3 v-if="selected.length>1">¿Está seguro de eliminar {{ selected.length }} registros?</h3>
						<h3 v-else>¿Está seguro de eliminar un registro?</h3>
					</div>

					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button
						class="mt-3"
						variant="danger"
						block
						title="Eliminar"
						@click="delete_all_devoluciones()"
					>
						Eliminar
					</b-button>
				</b-modal>
			</div>

			<!-- ======== Formulario de Busqueda ======== -->
			<b-form-group
				label-for="filter-input"
				label-align-sm="right"
				label-size="sm"
				class="mb-0"
				style="width: 100%; padding-bottom: 1em"
				v-show="rows > 0"
			>
				<b-input-group size="sm">
					<b-form-input
						id="filter-input"
						v-model="filter"
						type="search"
						placeholder="Buscar registros"
					></b-form-input>

					<b-input-group-append>
						<b-button :disabled="!filter" @click="filter = ''"
							>Limpiar</b-button
						>
					</b-input-group-append>
				</b-input-group>
			</b-form-group>
			<!-- ======================================== -->

			<div v-if="rows > 0">
				<div v-if="selected.length > 0">
					<pre>
                        Cantidad de registros: {{ rows }} | Filas seleccionadas: {{
							selected.length
						}}</pre
					>
				</div>
				<div v-else>
					<pre>Cantidad de registros: {{ rows }}</pre>
				</div>
				<b-button
					class="mb-4 ml-2"
					size="sm"
					style="color: white"
					title="Seleccionar Todo"
					@click="seleccionar_todas"
					:disabled="btn_select"
				>
					Seleccionar Todo
				</b-button>
				<b-button
					class="mb-4 ml-2"
					size="sm"
					style="color: white"
					title="Limpiar Seleccion"
					@click="limpiar_seleccion"
					:disabled="btn_limpiar"
				>
					Limpiar Seleccion
				</b-button>
			</div>
			<div v-else>
				<pre>Cantidad de registros: {{ rows }}</pre>
			</div>

			<section class="container">
				<b-table :fields="fields"
						 striped
						 sortable
						 responsive
						 hover
						 :items="tabla_devoluciones
								| FechaRange(dateDesde, dateHasta)
								| Producto(filter_producto)
								| Cliente(filter_cliente)"
						 show-empty
						 :sticky-header="false"
						 :no-border-collapse="false"
						 :per-page="perPage"
						 :current-page="currentPage"
						 ref="tablaregistros"
						 id="tablaregistros"
						 selectable
						 select-mode="multi"
						 @row-selected="seleccionar_una"
						 empty-text="No hay registros cargados"
						 empty-filtered-text="No hemos encontrado registros que coincidan con lo que está buscando"
						 :filter="filter"
						 @filtered="onFiltered">


					<template #cell(selected)="{ rowSelected }">
						<template v-if="rowSelected">
							<span aria-hidden="true">&check;</span>
							<span class="sr-only">Selected</span>
						</template>
						<template v-else>
							<span aria-hidden="true">&nbsp;</span>
							<span class="sr-only">Not selected</span>
						</template>
					</template>

					<template slot="cell(id_cliente)" slot-scope="data">
						{{ data.value.split('/')[4]}}
					</template>

					<template slot="cell(id_stock)" slot-scope="data">
						{{ data.value.split('/')[4]}}
					</template>

					<template slot="cell(action)" slot-scope="row">
						<div class="mt-3">
							<b-button-group>
								<b-button variant="info"
										  id="button-1"
										  title="Mostrar Información adicional"
										  :disabled="btn_mostrar"
										  @click="row.toggleDetails">
									{{ row.detailsShowing ? "Ocultar" : "Mostrar" }} Detalles
								</b-button>

								<b-button variant="warning"
										  id="button-2"
										  title="Editar este registro"
										  v-b-modal.modal_editar
										  @click="editardevolucion(row.item, row.index)"
										  :disabled="btn_editar">
									<v-icon class="mr-2"> mdi-pencil </v-icon>
									Editar
								</b-button>

								<b-button variant="danger"
										  id="button-3"
										  @click="showModalinfo(row.item, row.index)"
										  title="Eliminar este registro"
										  :disabled="btn_eliminar">
									<v-icon class="mr-2"> mdi-delete </v-icon>
									Eliminar
								</b-button>
							</b-button-group>
						</div>
					</template>
					<template #row-details="row">
						<b-card>
							<b-list-group horizontal>
								<b-list-group>
									<b-list-group-item>
										<b>Fecha en la que se Realizo:</b>
										{{ row.item.fecharealizacion }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Total:</b>
										{{ row.item.total }}
									</b-list-group-item>
									<b-card title="Productos: " v-if="row.item.componentes.length>0">

										<b-list-group horizontal>
											<div v-for="componente in row.item.componentes"
												 :key="componente.id">

												<b-list-group>
													<b-list-group-item>
														<b>Producto:</b>
														{{
productos.find(s=>{
                                                                    return s.id_producto == componente.producto.split('/')[4]
                                                                }).nombre
														}}
													</b-list-group-item>
													<b-list-group-item>
														<p>

															<b>Cantidad: </b>
															{{componente.cantidad}}
														</p>
													</b-list-group-item>
													<b-list-group-item>
														<p>

															<b>Precio: </b>
															{{componente.precio}}
														</p>
													</b-list-group-item>
												</b-list-group>

											</div>
										</b-list-group>
									</b-card>
									<b-list-group-item v-else>
										Sin Productos
									</b-list-group-item>

								</b-list-group>
								&nbsp;
							</b-list-group>
						</b-card>
					</template>
				</b-table>
				<b-container fluid>
					<b-col class="my-1">
						<b-pagination
							v-model="currentPage"
							align="center"
							pills
							:total-rows="totalRows"
							:per-page="perPage"
							aria-controls="table_devoluciones"
						>
						</b-pagination>
					</b-col>
				</b-container>

				<b-modal
					no-close-on-backdrop
					id="modal_editar"
					ref="my-modaledit"
					hide-footer
					title="Editar"
					ok-only
				>
					<devoluciones-update :devoluciones="editar" :updateTable="testFetch" />
				</b-modal>
				<!-- ================ELIMINAR UN PLAN======================== -->
				<b-modal
					no-close-on-backdrop
					id="modal_eliminar"
					ref="my-modal"
					hide-footer
					title="Eliminar"
					ok-only
				>
					<div class="d-block text-center">
						<h3>
							¿Esta seguro de eliminar los datos del devolucion '{{
								infoEliminar.devolucion.fecharealizacion}}'?
						</h3>
					</div>
					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button
						class="mt-3"
						variant="danger"
						block
						title="Eliminar"
						@click="deletedevolucion(infoEliminar.devolucion.id_devolucion)"
					>
						Eliminar
					</b-button>
				</b-modal>
			</section>
			<aside v-show="rows > 0">
				<div>
					<b-card-group deck>
						<b-card
							bg-variant="primary"
							text-variant="white"
							header="REGISTROS POR PAGINA"
							class="text-center"
						>
							<b-form-group label-for="per-page-select" class="mb-0">
								<b-form-select
									id="per-page-select"
									v-model="perPage"
									:options="pageOptions"
									size="sm"
								></b-form-select>
							</b-form-group>
						</b-card>
					</b-card-group>
				</div>
				<br />
				<div>
					<b-card-group deck>
						<b-card
							style="width: 100%"
							bg-variant="primary"
							text-variant="white"
							header="FILTRAR POR"
							class="text-center"
						>
							<div class="accordion" role="tablist" style="width: 100%">
								<b-card no-body>
									<b-card-header header-tag="header" class="p-1" role="tab">
										<b-button block
												  v-b-toggle.accordion-1
												  variant="info"
												  style="font-size: 0.82em">
											FECHA DE PAGO
										</b-button>
									</b-card-header>

									<b-collapse id="accordion-1"
												visible
												accordion="my-accordion"
												role="tabpanel">
										<b-card-body>
											<b-form-group style="color: black"
														  label="Desde"
														  label-for="fechaDesde"
														  @submit.stop.prevent="handleSubmit">
												<b-form-input id="fechaDesde"
															  v-model="dateDesde"
															  type="date"
															  aria-label="Desde"
															  placeholder="Ingrese una Fecha"
															  invalid-feedback="Complete este campo"
															  required>
												</b-form-input>
												{{ dateDesde }}
											</b-form-group>

											<b-form-group style="color: black"
														  label="Hasta"
														  label-for="fechaHasta"
														  @submit.stop.prevent="handleSubmit">
												<b-form-input id="fechaHasta"
															  v-model="dateHasta"
															  type="date"
															  placeholder="Ingrese una Fecha"
															  required>
												</b-form-input>
												{{ dateHasta }}
											</b-form-group>
											<div v-show="dateDesde != null && dateHasta != null">
												<b-button @click="limpiar_filtro_fecha()"
														  title="Limpiar">
													Limpiar
												</b-button>
											</div>
										</b-card-body>
									</b-collapse>
								</b-card>
								<b-card no-body>
									<b-card-header header-tag="header" class="p-1" role="tab">
										<b-button block
												  v-b-toggle.accordion-2
												  variant="info"
												  style="font-size: 0.82em">
											PRODUCTO
										</b-button>
									</b-card-header>
									<b-collapse id="accordion-2"
												visible
												accordion="my-accordion"
												role="tabpanel">
										<b-card-body>
											<b-form-group id="input-group-4">
												<v-autocomplete id="letra"
																v-model="filter_producto"
																:items="options_producto"
																type="text"
																solo
																filled></v-autocomplete>
												<div v-show="filter_producto != null">
													<b-button @click="filter_producto = null"
															  title="Limpiar">
														Limpiar
													</b-button>
												</div>
											</b-form-group>
										</b-card-body>
									</b-collapse>
								</b-card>
								<b-card no-body>
									<b-card-header header-tag="header" class="p-1" role="tab">
										<b-button block
												  v-b-toggle.accordion-3
												  variant="info"
												  style="font-size: 0.82em">
											CLIENTE
										</b-button>
									</b-card-header>
									<b-collapse id="accordion-3"
												visible
												accordion="my-accordion"
												role="tabpanel">
										<b-card-body>
											<b-form-group id="input-group-4">
												<v-autocomplete id="letra"
																v-model="filter_cliente"
																:items="options_cliente"
																type="text"
																solo
																filled></v-autocomplete>
												<div v-show="filter_cliente != null">
													<b-button @click="filter_cliente = null"
															  title="Limpiar">
														Limpiar
													</b-button>
												</div>
											</b-form-group>
										</b-card-body>
									</b-collapse>
								</b-card>
							</div>
						</b-card>
					</b-card-group>
				</div>
			</aside>
		</div>
	</v-app>
</template>

<script>
    let api = new URL("http://localhost:8081");
	api.pathname = "devoluciones/";
	//api.port = 8000;
	//api.port = 8081;

    import devolucionesAlta from "./DevolucionesAlta.vue";
    import devolucionesUpdate from "./DevolucionesUpdate.vue";
	import axios from "axios";

	import { APIControler } from "@/store/APIControler";
	import swal from "sweetalert";

	export default {
        components: { devolucionesAlta, devolucionesUpdate },
		data() {
			return {
				tabla_devoluciones: [],
				productos: [],
				stockusado: [],
				options_cliente: [],
				filter_cliente: null,
                stocks: [],
				fields: [
					{ key: "selected", label: ""},
					{ key: "id_devolucion", label: "ID", sortable: true },
                    { key: "tipo_dev", label: "Tipo", sortable: true },
                    { key: "fecharealizacion", label: "Fecha de Realizacion", sortable: true },
					{ key: "gastototal", label: "Gasto Total", sortable: true },
					{ key: "id_cliente", label: "Cliente", sortable: true },
                    { key: "id_stock", label: "Stock", sortable: true },
					{ key: "action", label: "Acciones", variant: "secondary" },
				],
				modal: false,
				clientes: [],
				dateDesde: null,
				productonuevo: {},
				dateHasta: null,
                options_producto: [
                    { value: null, text: "Elija un Producto", selected: true },
                ],
                productoelegido: {},
				editar: {},
				filter: null,
                filter_producto: null,
				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				pageOptions: [10, 20, 40, 100, { value: 10000, text: "Todos" }],
				selected: [],
				infoEliminar: {
					id: "modal_eliminar",
					devolucion: -1,
				},
				btn_del_full: true,
				btn_mostrar: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
				btn_limpiar: true,
			};
		},
		computed: {
			rows() {
				return (this.totalRows = this.tabla_devoluciones.length);
			},
			sortOptions() {
				// Funcion para filtrar
				return this.fields
					.filter((f) => f.sortable)
					.map((f) => {
						return { text: f.label, value: f.key };
					});
			},
		},

		methods: {
			
			async testFetch() {
				try {
					const res = await fetch(api);
					const data = await res.json();

					this.tabla_devoluciones = data.results;
					this.tabla_devoluciones.forEach((element) => {
                        let opcionCli = {};
                        opcionCli.value = element.id_cliente.split('/')[4];
                        opcionCli.text = this.getCliente(element.id_cliente.split('/')[4]).nombre;
                        if (
                            this.options_cliente.find((x) => x.value == opcionCli.value)
                        ) {
                            console.log(opcionCli, " ya se encuentra en el listado");
                        } else {
                            this.options_cliente.push(opcionCli);
                        }
						element.componentes.forEach((prod) =>
						{
                            let opcionFarm = {};
                            opcionFarm.value = prod.producto;
                            opcionFarm.text = this.getProducto(prod.producto.split("/")[4]).nombre;
                            if (
                                this.options_producto.find((x) => x.value == opcionFarm.value)
                            ) {
                                console.log(opcionFarm, " ya se encuentra en el listado");
                            } else {
                                this.options_producto.push(opcionFarm);
                            }
						})
                    });
					this.checkeodemodal();
				} catch (error) {
					console.log(error);
				}
			},
            checkeodemodal() {
                if (this.$route.query.abrir == 'true') {
                    this.$refs["modalalta"].show();
                }
			},
            redirect() {
                if (!this.$session.exists()) {
                    window.location.replace('/login');
                }
                if (this.$session.get('tipocuenta') != 'administrador') {
                    window.location.replace('/');
                }

            },
			//Funcion para mostrar el modal
			showModal() {
				this.$refs["my-modal"].show();
			},
			//Funcion para esconder el modal
			hideModal() {
				this.$refs["my-modal"].hide();
			},

			info(id) {
				alert("id: " + id);
			},

			showModalinfo(item, index) {
				this.infoEliminar.devolucion = item;
				this.showModal();
			},

			//-----Funciones de seleccion
			//Selecciona una a una
			seleccionar_una(items) {
				this.selected = items;
				if (this.selected.length > 0) {
					this.btn_del_full = false;
					this.btn_limpiar = false;
					if (this.selected.length > 1) {
						this.btn_mostrar = true;
						this.btn_editar = true;
						this.btn_eliminar = true;
					}
					if (this.selected.length == this.rows) {
						this.btn_select = true;
					} else {
						this.btn_select = false;
					}
				} else {
					this.btn_del_full = true;
					this.btn_mostrar = false;
					this.btn_editar = false;
					this.btn_eliminar = false;
					this.btn_limpiar = true;
				}
			},
			//Selecciona todas
			seleccionar_todas() {
				this.$refs.tablaregistros.selectAllRows();
				this.btn_del_full = false;
				this.btn_mostrar = true;
				this.btn_editar = true;
				this.btn_eliminar = true;

				this.btn_select = true;
				this.btn_limpiar = false;
			},
			//Limpia todas las selecciones
			limpiar_seleccion() {
				this.$refs.tablaregistros.clearSelected();
				this.btn_del_full = true;
				this.btn_mostrar = false;
				this.btn_editar = false;
				this.btn_eliminar = false;

				this.btn_select = false;
				this.btn_limpiar = true;
			},

			//Alta de medicamento
			altadevolucion() {},

			//Editar medicamento
			editardevolucion(item, index) {
				this.editar = item;
			},
            async actualizarStockProducto1(devolucion) {
                let stockfinal = this.getStock(devolucion.id_stock.split('/')[4])
                this.stockusado.nombre = stockfinal.nombre;
                this.stockusado.id_reparto = stockfinal.id_reparto;
				this.stockusado.fechastock = stockfinal.fechastock;
                this.stockusado.cerrado = stockfinal.cerrado;
                this.stockusado.componentes = stockfinal.componentes;
                devolucion.componentes.forEach((produc) => {
                    let id = produc.producto.split('/')[4];
                    if (!this.checkprodstock(id)) {
                        this.productonuevo.producto = produc.producto;
                        this.productonuevo.cantidad = produc.cantidad;
                        this.stockusado.componentes.push(this.productonuevo);
                    } else {
                        this.stockusado.componentes.forEach((compon) => {
                            if (compon.producto.split('/')[4] == id) {
                                compon.cantidad = parseInt(compon.cantidad) - parseInt(produc.cantidad);
                            }
                        });
                    }
                    console.log("Id del producto en venta:", id);
                });
                console.log(this.stockusado);
                await axios
                    .put(
                        "http://localhost:8081/stocks/" +
                        stockfinal.id_stock +
                        "/",
                        this.stockusado
                    );
            },
            async actualizarStockProducto(id, stock) {
                console.log(this.getProducto(id));
                let productofinal = this.getProducto(id);
                this.productoelegido.id_producto = productofinal.id_producto
                this.productoelegido.nombre = productofinal.nombre
                this.productoelegido.tipo_producto = productofinal.tipo_producto
                this.productoelegido.letra = productofinal.letra
                this.productoelegido.numero_producto = productofinal.numero_producto
                this.productoelegido.codigo_unico = productofinal.codigo_unico
                this.productoelegido.stocktotal = parseInt(productofinal.stocktotal) - parseInt(stock)
                this.productoelegido.precioref = productofinal.precioref
                console.log(this.productoelegido)
                await axios
                    .put(
                        "http://localhost:8081/productos/" +
                        this.productoelegido.id_producto +
                        "/",
                        this.productoelegido
                    );
            },
            async getProductos() {
				let serviciosAPI = new APIControler();
				serviciosAPI.apiUrl.pathname = "productos/";
				this.productos= await serviciosAPI.getData()
			},
            async getClientes() {
                let serviciosAPI = new APIControler();
                serviciosAPI.apiUrl.pathname = "clientes/";
                this.clientes = await serviciosAPI.getData()
            },
            async getStocks() {
                let serviciosAPI = new APIControler();
                serviciosAPI.apiUrl.pathname = "stocks/";
                this.stocks = await serviciosAPI.getData()
            },
            getProducto(id){
                if(this.productos==[]){
                    return getProductos().then(()=>{
                        return this.getProducto(id)
                    })
                }
                else{
                    return this.productos.find(s=>{
                        return s.id_producto == id
                    })
                }
			},
            getCliente(id) {
                if (this.clientes == []) {
                    return getClientes().then(() => {
                        return this.getCliente(id)
                    })
                }
                else {
                    return this.clientes.find(s => {
                        return s.numero_cliente == id
                    })
                }
            },
            getStock(id) {
                if (this.productos == []) {
                    return getStocks().then(() => {
                        return this.getStock(id)
                    })
                }
                else {
                    return this.stocks.find(s => {
                        return s.id_stock == id
                    })
                }
            },
			//Funcion para eliminar el medicamento
            async deletedevolucion(id, devolucion) {
				axios
                    .delete("http://localhost:8081/devoluciones/" + id + "/")
					.then((datos) => {
						if (this.getStock(devolucion.id_Stock.split('/')[4]).cerrado == false)
						{
							this.actualizarStockProducto1(devolucion);
						} else
						{
                            devolucion.componentes.forEach(element => {
                                console.log("Longitud");
                                this.actualizarStockProducto(element.producto.split('/')[4], element.cantidad);
                            });
						}
						swal("Eliminacion Exitosa", " ", "success");
						console.log(datos);
						this.hideModal();
					})
					.catch((error) => {
						swal("¡ERROR!", "Se ha detectado un problema ", "error");
						console.log(error);
						this.hideModal();
					})
					.finally(() => this.testFetch());
			},

			//Funcion para eliminar todos los medicamentos seleccionados
			async delete_all_devoluciones() {
				var cantidad = this.selected.length;

				try {
					for (var i = 0; i < cantidad; i++) {
						axios.delete(
							"http://localhost:8081/devoluciones/" + this.selected[i].id_devolucion + "/"
						);
						if (this.selected.length == 0) {
							console.log("Eliminacion Exitosa");
							break;
						}
					}
					this.hideModal();
					swal("Eliminacion Exitosa", " ", "success");
				} catch (error) {
					this.hideModal();
					swal("¡ERROR!", "Se ha detectado un problema ", "error");
					console.log(error);
				} finally {
					this.testFetch();
				}
			},

			limpiar_filtro_fecha() {
				this.dateDesde = null;
				this.dateHasta = null;
			},

			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},

		},

		beforeMount() {
			this.getProductos();
            this.getClientes();
			this.testFetch();
			this.redirect();
            this.getStocks();
		},
	};
</script>

